const ButtonGroup = (props) => {
  const [countA, setCountA] = useState(0);
  const [countB, setCountB] = useState(0);
  useEffect(() => {
    console.log("done");
  });
  return (
    <div className="buttonGroup">
      <div>
        <ButtonA increment={() => setCountA((count) => count + 1)} />
        <IndicatorA count={countA} />
      </div>
      <div>
        <ButtonB increment={() => setCountB((count) => count + 1)} />
        <IndicatorB count={countB} />
      </div>
    </div>
  );
};

