const ButtonGroupWithUseCallback = (props) => {
  const [countA, setCountA] = useState(0);
  const [countB, setCountB] = useState(0);
  useEffect(() => {
    console.log("done");
  });

  const incrementA = useCallback(() => {
    setCountA((count) => count + 1);
  }, [setCountA]);
  const incrementB = useCallback(() => {
    setCountB((count) => count + 1);
  }, [setCountB]);

  return (
    <div className="buttonGroup">
      <div>
        <ButtonA increment={incrementA} />
        <IndicatorA count={countA} />
      </div>
      <div>
        <ButtonB increment={incrementB} />
        <IndicatorB count={countB} />
      </div>
    </div>
  );
};

const MemoizedButtonGroupWithUseCallback = (props) => {
  const [countA, setCountA] = useState(0);
  const [countB, setCountB] = useState(0);
  useEffect(() => {
    console.log("done");
  });

  const incrementA = useCallback(() => {
    setCountA((count) => count + 1);
  }, [setCountA]);
  const incrementB = useCallback(() => {
    setCountB((count) => count + 1);
  }, [setCountB]);

  return (
    <div className="buttonGroup">
      <div>
        <MemoizedButtonA increment={incrementA} />
        <MemoizedIndicatorA count={countA} />
      </div>
      <div>
        <MemoizedButtonB increment={incrementB} />
        <MemoizedIndicatorB count={countB} />
      </div>
    </div>
  );
};

const Hoge = (props) => {
  const [state, setState] = useState(false);
  const start = performance.now();
  const lightFunc = () => {
    console.log("easy");
  };
  const end = performance.now();
  console.log(`lightFunc: ${end - start}`);
  return (
    <div>
      <button
        onClick={() => {
          setState((prev) => !prev);
        }}
      >
        
      </button>
    </div>
  );
};

const Fuga = (props) => {
  const [state, setState] = useState(false);
  const start = performance.now();
  const lightFunc = useCallback(() => {
    console.log("easy");
  }, []);
  const end = performance.now();
  console.log(`lightFuncWithUseCallback: ${end - start}`);
  return (
    <div>
      <button
        onClick={() => {
          setState((prev) => !prev);
        }}
      >
        
      </button>
    </div>
  );
};

